.container {
	-ms-flex: 1 1 auto;
	    flex: 1 1 auto;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
}

.title {
	-ms-flex: 0 0 auto;
	    flex: 0 0 auto;
	font-size: 1.1em;
	font-weight: 600;
	margin-bottom: 10px;
	text-align: center;
}

.content {
	-ms-flex: 1 1 auto;
	    flex: 1 1 auto;
	border: 1px solid #666;
	border-radius: 20px;
	padding: 30px 50px;
	word-wrap: break-word;
	overflow: auto;
	line-height: 1.4em;
}



